@media (max-width: 900px), screen and (orientation: portrait) {
 html { font-size: 10pt; }

 .layout,
 .layout__sidebar,
 .layout__main {
 & > * { 
 width: 100vw;
 max-width: 100vw;
 display: block;
 margin-left: 0;
 margin-right: 0;
 }
 .panel {
 width: calc(100vw - 6px);
 max-width: calc(100vw - 6px);
 }
 }

 .layout > * { margin: 0; }

 #nav { display: none; }

 #mobile-nav {
 display: block;
 white-space: nowrap;
 text-overflow: clip;
 max-width: 100vw;
 overflow-x: scroll;
 }

 .mobile-nav-container { margin: 12px 12px 18px 12px; }

 .panel { border: none; padding: 3px; }

 #kyouko { display: none; }
}

@media (max-width: 300px) {
 .panel > div { margin: 3px; }
 .mobile-nav-container {
 margin: 6px 6px 12px 6px;
 }
 h1, h2, h3, h4, h5, h6 { font-size: inherit; font-weight: bold; }
}